import React, { PureComponent, Suspense } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../utils";
import { isTagShop, suiteTrack } from "../../../../actions/api";
import ViewCount from '../../Elements/ViewCount';

const Network = React.lazy(() => import('../../Elements/Network/Network'));
const ShopButton = React.lazy(() => import('../../Elements/ShopButton/ShopButton'));


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    const { Post, onClickPopUpSlider, itemIndex } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.mediaHeight)
    if (Post.isPopUp) {
      onClickPopUpSlider(itemIndex, Post)
    }
  }

  render() {
    const { itemIndex, isVisible, clickToShowPopUp, ThemeStyle, CardStyle, Post, index } = this.props;

    const {count, networkId } = Post
    const { style, content, } = CardStyle
    const cardStyle = { backgroundColor: style.backgournd };
    const isShopBtn = isTagShop && CardStyle.shopText != '' && (Post.isProduct) && !content.status ? true : false;
    const themeMode = (style.mode == 0 && isTagShop) ? 'light' : 'dark'
    const reelCount = ([3,5].includes(Post.type)) && CardStyle.viewCountStatus === 1 && count?.views > 0 ? true : false

    return <div data-id={Post.id} id={`tb_rt_id-${Post.id}`} className="tb_rt_post_wrapper" ref={this.mediaHeight} style={{ padding: style.padding / 2 }} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} area-label={Post.content.text} role="article">
      <div className={`tb_rt_post_in tb_icon_animate ${!isTagShop ? 'tb_rt_post_in_animate' : ''}`} style={cardStyle} onClick={clickToShowPopUp(itemIndex, Post)} role="button" aria-label="Popup" tabIndex={0}>
        <div className="tb_rt_post_media_wrapp">

          {reelCount ? <ViewCount count={count?.views} /> : null}

          <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} isVisible={isVisible} index={index} />

          {!isTagShop ?
            <div className="tb_rt_play_ico">
              <div className="tb__icon tb-play"><div></div></div>
            </div>
          : null}

          <div className="tb_rt_post_details">
            <div className="tb_rt_social_">
              <Suspense fallback={<></>}><Network networkClass={'tb_rt_social_ico_'} networkId={networkId} iconStyle={CardStyle.icon} /></Suspense>
            </div>
            {isShopBtn ? <Suspense fallback={<></>}><ShopButton CardStyle={CardStyle} btnClass={'tb_rt_btn_wrap'} /></Suspense> : null}
          </div>
          <div className={`tb_rt_post_overlay tb_rt_post_overlay-${themeMode}`}><div></div></div>
        </div>
      </div>
    </div>
  }
}
